.album-component {
  cursor: pointer;
  padding: 50px 20px;
}

.album-component:hover {
  transform: scale(1.05);
  transition: transform 0.3s ease-in-out;
}

.album-link {
  text-decoration: none;
}

.album-component .album-cover{
  width: 100%;
  max-width: 200px;
}

.album-title {
  text-align: start;
  margin-top: 10px;
  width: 100%;
  display: block;
  font-weight: 400;
  max-height: 75px;
  overflow: hidden;
  color: #000;
}